//reducers/product.js
const initialState = {}
const decreaseInventory=(product) => {
	return {
		...product,
	    inventory:product.inventory - 1
	}
}
export default function products(state=initialState,action){
	switch (action.type) {
		case 'showProducts':
		   return action.products
		case 'proAddToCart':
		   //console.log(action.id)
		   return {
		   	  ...state,
		   	  [action.id]:decreaseInventory(state[action.id])
		   }
		default:
		   return state
	}

}


