const initialState = {}

const addProduct = (state, product) => {
	if(!state[product.id]){
		return {
			...state,
			[product.id]:{ ...product,num:1}
		}
	}
	return state
}
const cartAdd = (state, productId) => {
	return {
		...state,
		[productId]:{...state[productId],num:(state[productId].num || 0)+1,inventory:state[productId].inventory-1} 
	}
}

export default function cart(state = initialState, action){
	switch(action.type){
	  case 'addCartProducts':
	    return addProduct(state,action.item)
	  case 'cartAdd':
	    return cartAdd(state,action.id)
	  default:
	    return state
	}
}


