import React from 'react'
import './style.less'
class CityList extends React.Component {
 
	render() {
	    return (
            <div className=''>
                <ul className="cityList">
                    <li onClick={this.clickFun.bind(this,'重庆')}><span>重庆</span></li>
                    <li onClick={this.clickFun.bind(this,'北京')}><span>北京</span></li>
                    <li onClick={this.clickFun.bind(this,'上海')}><span>上海</span></li>
                    <li onClick={this.clickFun.bind(this,'厦门')}><span>厦门</span></li>
                    <li onClick={this.clickFun.bind(this,'深圳')}><span>深圳</span></li>
                    <li onClick={this.clickFun.bind(this,'福建')}><span>福建</span></li>
                    <li onClick={this.clickFun.bind(this,'东京')}><span>东京</span></li>
                    <li onClick={this.clickFun.bind(this,'广州')}><span>广州</span></li>
                </ul>
            </div>
	    )
	}
  clickFun(cityName){
     this.props.changCityFun(cityName)
  }
	
}

export default CityList




