import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import './style.less'
import {Link} from 'react-router'

class List extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state = {
         account:0
       }
    }
	render(){
	   return (
          <div className="cartPage">
               {
                  this.props.cartList.length
                  ?<div>
                      <ul className="cartList">
                      {
                         this.props.cartList.map((item,index) => {
                            return <li key={index}>
			                             <img src={item.src}/>
			                             <div className="itemContent">
			                                <h4>{item.name}</h4>
			                                <p>单价：{(item.price).toFixed(2)}元</p>
			                                <p>剩余{item.inventory}件</p>
			                                <div className="countBox">
			                                   <span className="reduceBtn">-</span>
			                                   <i>{item.num}</i>
			                                   <span className="addBtn" onClick={this.addFun.bind(this,item.id)}>+</span>
			                                </div>
			                             </div>
			                         </li>

                         })

                       } 
                      </ul>
                      <p className="accountTxt">合计金额：<span>{this.state.account}</span>元</p>
                  </div>
                  :<div className="cartIsEmpty">
                      <p>购物车暂无内容</p>
                      <div><Link to="/mallHome" className="linkBtn">去添加</Link></div>
                   </div>


               }
          </div>
	   )
	}
	componentDidMount(){
	   this.getAccount()
	}
	addFun(id){
       this.props.addFun(id)
       setTimeout(()=>{ this.getAccount()},20)
      
	}
	getAccount(){
	   let priceSum=0
       this.props.cartList.map((item,index) => {
          priceSum+=item.num*item.price
       })
       this.setState({
          account:priceSum
       })
	}
}

export default List


