import React from 'react'
import { render } from 'react-dom' 
import Children from './subPage/children'
import { connect } from 'react-redux'

import './subPage/style.less'
// 定义组件 
class LHome extends React.Component {
   render() { // return 里面写jsx语法 
      return ( 
        <div>
         <p>最基础的模版：hello world</p>
         <p onClick={this.clickFun.bind(this)}  className='btnstyle'>点击触发事件</p>

         <h3 className="tit tit_B">子组件:</h3>
         <Children text="SSSSS" cityName={this.props.userinfo.cityName}/>

         <h3 className="tit tit_B">redux:</h3>
         <p className="demoBox">
             redux.flag:{this.props.flag.flagStr} 
             <input type="text" className="shortInput"/>
             <span onClick={this.changeFlagFun.bind(this)} className='btnstyle'>修改redux.flag</span>
         </p>
        </div>

      )
   } 
   clickFun(e){
      console.log('lxf:click now')
   }
   changeFlagFun(){
      var val=document.getElementsByClassName('shortInput')[0].value
      this.props.flagActions(val)
   }
}

// -------------------redux react 绑定--------------------

function mapStateToProps(state) {
    return {
        userinfo: state.userinfo,
        flag: state.flag
    }
}

function mapDispatchToProps(dispatch) {
    return {
       //flagActions: bindActionCreators({type:"changeFlag",data}}, dispatch),
       flagActions(data){
          dispatch({
            type: 'changeFlag',
            data:data
          })
       }
    }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(LHome)



