import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import {Link} from 'react-router'
import './style.less'

class List extends React.Component {
	render() {
	   return (
          <div>
             <h3>列表</h3>
             <ul className="list">
                {

                  this.props.listData.length
                  ?(
                     this.props.listData.map((item,index)=>{
                       return  <li key={index}>
                          <Link to={`/MallDtl/${item.id}`}>
  				                  <img src={item.src}/>
  				                  <div className="content">
  				                     <p className="itemTit">{item.name}</p>
  				                     <p className="itemInfo">单价：{(item.price).toFixed(2)}元</p>
                               <p className="itemInfo">库存{item.inventory}件</p>
                               <span className="addToCartBtn" onClick={this.addToCartFun.bind(this,item)}>+</span>
  				                  </div>
                          </Link>
				                 </li>

                     })
                     
                  )
                  :"列表加载中"
                }
             </ul>
          </div>
	   )
	}
	componentDidMount(){
	}
  addToCartFun(item,event){
    event.preventDefault();
    this.props.addToCartFun(item)
    //console.log(productId+"sseee")
  }
}

export default List


