import React from 'react'
import './style.less'
class Children extends React.Component {
    constructor(props, context) {
      super(props, context);
      this.state = {
         now: Date.now(),
         data: []
      }
    }
	render() {
	    return (
            <div className='borderStyle'>
                这是子组件 --<span>父级传过来的值是：{this.props.text}</span>
                <p>组件.state.now:{this.state.now}  <span onClick={this.clickFun.bind(this)} className="btnstyle">更改state</span></p>
                <div>
                   {
                      this.state.data.length
                      ? <p>fetch请求的数据：{this.state.data[0].name}</p>
                      : <p>正在请求数据中..</p>
                   }
                </div>
                <div>{this.props.cityName}</div>
            </div>
	    )
	}
	clickFun(e) {
      this.setState({
         now: Date.now()
      })
	}
	//生命周期
	componentDidMount() {
       console.log('children组件已渲染完成！！！')
       fetch('https://www.easy-mock.com/mock/5a29f3122763647eeca67466/crm/orderList').then((res)=>{
          return res.json()
       }).then((res)=>{
          console.log(JSON.stringify(res))
          if(res.data.length){
            console.log('！！！')
            this.setState({
                data: res.data
            })
          }
       })
	}
}

export default Children




