import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'

import MallHeader from '../../components/mallHeader'
import MallSlider from '../../components/mallSlider'
import List from './subPage/list'
import { connect } from 'react-redux'
import {showProducts} from '../../actions/product'
import {proAddToCart} from '../../actions/product_addToCart'
import {cartProducts,cartAdd} from '../../actions/cart'
import Alert from '../../components/alert'

class mallHome extends React.Component {
    constructor(props,context){
       super(props,context);
       this.shouldComponentUpdate=PureRenderMixin.shouldComponentUpdate.bind(this);
       this.state = {
         listData:[],
         alertTxt:''
       }
    }
	render() {
	   return(
          <div>
            <MallHeader title="react-首页"  back={false} curCity={this.props.userinfo.cityName}/>
            <MallSlider />
            <List listData={this.props.products} addToCartFun={this.proAddToCart.bind(this)}/>
            <Alert alertTxt={this.state.alertTxt}/>
          </div>
	   )
        
	}
	componentDidMount(){
	   //this.getList()
	   this.props.showProducts_action()
	   //this.proAddToCart()
	   //setTimeout(()=>{  this.props.cartAdd_action('002') },5000)
	}
    
    proAddToCart(item){
       if(this.props.cartInfo[item.id]){
         this.showAlert('请勿重复添加')
       }else{
         this.props.addToCart_action(item)
         this.showAlert('已成功添加到购物车')
       }

    }
    showAlert(alertxt){
       this.setState({
          alertTxt:alertxt
       })
       setTimeout(()=>{
          this.setState({
          alertTxt:''
       })
       },700)
    }

	//获取远程easy-mock数据，现在已不使用
	getList(){
	   fetch('https://www.easy-mock.com/mock/5a28a164b5c06d61d33714e9/llxf/react-demo-list').then((res)=>{
	       return res.json()
	   }).then((res)=>{
	       this.setState({
	          listData:res.data
	       })
	       console.log(this.state.listData)
	   })
	}


}

// -------------------redux react 绑定--------------------
const parseProducts = (products) => {
	let mapProduct = []
	for(let key in products) {
	  mapProduct.push(products[key])
	}
	return mapProduct
}

function mapStateToProps(state) {
    return {
        userinfo: state.userinfo,
        products: parseProducts(state.products),
        cartInfo: state.cart,
    }
}

function mapDispatchToProps(dispatch) {
    return {
       showProducts_action(){
          dispatch(showProducts())
       },
       addToCart_action(item){
          dispatch(proAddToCart(item.id))
          dispatch(cartProducts(item))
       },
       cartAdd_action(id){
          dispatch(cartAdd(id))
       }
    }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(mallHome)


