import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import MallHeader from '../../components/mallHeader'
import CityList from './subPage/cityList'
import { connect } from 'react-redux'

class MallCity extends React.Component {
	constructor(props,context) {
	   super(props,context);
	   this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
	}
	render() {
	   return (
           <div>
               <MallHeader title="选择城市" back={true}/>
               <div className="curCityTxt">当前城市：<span className="curCity">{this.props.userinfo.cityName}</span></div>
               <CityList changCityFun={this.changCityFun.bind(this)}/>
           </div>
	   )
	}
	changCityFun(newCity){
	   this.props.changeCity({cityName:newCity})
	}
}


// -------------------redux react 绑定--------------------

function mapStateToProps(state) {
    return {
        userinfo: state.userinfo
    }
}

function mapDispatchToProps(dispatch) {
    return {
       changeCity(data){
          dispatch({
             type:'USERINFO_UPDATE',
             data
          })
       }
    }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MallCity)


