import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'

class Alert extends React.Component {
	render() {
        return (
          <div>
			 {
	             this.props.alertTxt
	              ? <div className="alertTip">
	                  <p>{this.props.alertTxt}</p>
	                 </div>
	              :''
	          }
	       

          </div>
          )


	}
}


export default Alert  


