import React from 'react'
import { render } from 'react-dom' 
import { connect } from 'react-redux'
import List from './subPage/list'
import MallHeader from '../../components/mallHeader'
import {cartAdd} from '../../actions/cart'
// 定义组件 
class MallCart extends React.Component {
   render() { // return 里面写jsx语法 
      return ( 
        <div>
         <MallHeader title="购物车"/>
         <List cartList={this.props.cartList} addFun={this.cartAddfun.bind(this)}/>
        </div>

      )
   } 
    componentDidMount(){
      console.log(this.props.cartList)
    }
    cartAddfun(id){
      this.props.cartAdd_action(id)
    }
}


// -------------------redux react 绑定--------------------
const parseProducts = (products) => {
	let mapProduct = []
	for(let key in products) {
	  mapProduct.push(products[key])
	}
	return mapProduct
}

function mapStateToProps(state) {
    return {
        cartList: parseProducts(state.cart),
        cartInfo: state.cart,
    }
}

function mapDispatchToProps(dispatch) {
    return {
       cartAdd_action(id){
          dispatch(cartAdd(id))
       }
    }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MallCart)


